
.login .login-methods .login-methods-divider {
  margin: 3em 0;
  position: relative;
}
.login .login-methods .login-methods-divider:before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #ddd;
}
.login .login-methods .login-methods-divider span {
    position: relative;
    z-index: 2;
    background: #fff;
    padding: 0 5px;
}
.button.button--fb {
  border: none;
  background-color: #4267b2;
  color: #fff;
  transition: all 0.5s ease;
  cursor: pointer;
}
.button.button--fb:hover, .button.button--fb:focus {
    background-color: #34518d;
}



